import heroImg from "../../assets/bg-image-hero.png";

const Hero = () => {
  return (
    <div className="relative h-screen w-full overflow-hidden">
      <img 
        src={heroImg.src} 
        alt="Hero background"
        className="w-full h-full object-cover"
      />
    </div>
  )
}

export default Hero;
